import { CosmosInputNode, CosmosInputLink } from '@cosmograph/cosmos'
import { Search, type SearchConfigInterface, merge, SearchEvents, SearchAccessorOption } from '@cosmograph/ui'
import { CosmographSearchInputConfig, CosmographSearchConfigInterface, defaultCosmographSearchConfig } from './config'
import { Cosmograph, Filter } from '../cosmograph'

export class CosmographSearch<N extends CosmosInputNode, L extends CosmosInputLink> {
  private _cosmograph: Cosmograph<N, L>
  private _config: CosmographSearchInputConfig<N> = {}
  private _data: N[] = []
  private _filter: Filter<CosmosInputNode>
  private _defaultAccessors: SearchAccessorOption<N>[] = []

  public search: Search<N>

  public constructor (cosmograph: Cosmograph<N, L>, targetElement: HTMLElement, config?: SearchConfigInterface<N>) {
    this._cosmograph = cosmograph
    this._config = merge(defaultCosmographSearchConfig, config ?? {}) as CosmographSearchInputConfig<N>
    this.search = new Search<N>(targetElement, this._createSearchConfig(config))
    this._filter = this._cosmograph.addNodesFilter()
    this._filter.onDataAdded = () => {
      this._updateData()
    }
    this._updateData()
  }

  /**  `setConfig`: Sets config for the `Search` instance. */
  public setConfig (config?: CosmographSearchInputConfig<N>): void {
    const nextConfig = merge(defaultCosmographSearchConfig, config ?? {})
    // Check if `accessors` is still empty after merging configs,
    // if yes - use default `accessors`
    if (this._data.length && nextConfig.accessors === undefined) {
      nextConfig.accessors = this._defaultAccessors as (SearchAccessorOption<CosmosInputNode>[] & SearchAccessorOption<N>[]) | undefined
    }
    this.search.setConfig(this._createSearchConfig(nextConfig))
    this._config = nextConfig
  }

  private _updateData (): void {
    const data = this._cosmograph.data.nodes
    if (data?.length) {
      this._data = data as unknown as N[]
      this.search.setData(this._data)
      if (this._config.accessors === undefined) {
        this._defaultAccessors = this._createDefaultAccessorOptions(this._data)
        this.setConfig({ accessors: this._defaultAccessors })
      }
    }
  }


  /**  `getConfig`: Returns current `Search` configuration. */
  public getConfig (): CosmographSearchConfigInterface<N> {
    return this._config
  }

  /**  `remove`: Destroys current `Search` instance. */
  public remove (): void {
    this.search.destroy()
  }

  /**  `setListState`: Manages the state of the `Search` suggestions/accessors dropdown list. */
  public setListState (state: boolean): void {
    this.search.setListState(state)
  }

  /**  `clearInput`: Clears the text input of `Search` instance. */
  public clearInput (): void {
    this.search.clearInput()
  }

  private _createDefaultAccessorOptions = (data: N[]): SearchAccessorOption<N>[] => {
    if (data.length > 0 && data[0]) {
      return Object.keys(data[0]).map((key) => {
        return { label: key, accessor: (n: N) => String(n[key as keyof typeof n]) }
      })
    } else {
      return [{ label: 'id', accessor: (n: N) => n.id }]
    }
  }

  private _onSelectResult (node: N): void {
    this._cosmograph.pause()
    this._cosmograph.zoomToNode(node)
    this._cosmograph.selectNode(node)
    this._config.onSelectResult?.(node)
  }

  private _createSearchConfig (config?: CosmographSearchInputConfig<N>): SearchConfigInterface<N> {
    return {
      ...config,
      events: {
        onSelect: this._onSelect.bind(this),
        onSearch: this._onSearch.bind(this),
        onEnter: this._onEnter.bind(this),
        onAccessorSelect: this._onAccessorSelect.bind(this),
      },
    }
  }

  private _onSelect: Exclude<SearchEvents<N>['onSelect'], undefined> = (...args) => {
    this._onSelectResult(...args)
    // this._config.onSelect?.(...args)
  }

  private _onSearch: Exclude<SearchEvents<N>['onSearch'], undefined> = (...args) => {
    this._config.onSearch?.(...args)
  }

  private _onEnter: Exclude<SearchEvents<N>['onEnter'], undefined> = (...args) => {
    this._config.onEnter?.(...args)
  }

  private _onAccessorSelect: Exclude<SearchEvents<N>['onAccessorSelect'], undefined> = (...args) => {
    this._config.onAccessorSelect?.(...args)
  }
}

export type { SearchConfigInterface as CosmographSearchConfigInterface, CosmographSearchInputConfig }
