import { CosmosInputNode, CosmosInputLink } from '@cosmograph/cosmos'
import { Timeline, TimelineConfigInterface, type TimelineData, TimelineEvents, merge } from '@cosmograph/ui'
import { Cosmograph, Filter } from '../cosmograph'
import { FilterType } from '../cosmograph/types'
import { CosmographTimelineInputConfig, CosmographTimelineConfigInterface, defaultCosmographTimelineConfig } from './config'

export class CosmographTimeline<Datum> {
  private _cosmograph: Cosmograph<CosmosInputNode, CosmosInputLink>
  private _config: CosmographTimelineConfigInterface<Datum> = {}
  private _filter: Filter<CosmosInputNode | CosmosInputLink>

  public timeline: Timeline

  public constructor (cosmograph: Cosmograph<CosmosInputNode, CosmosInputLink>, targetElement: HTMLElement, config?: CosmographTimelineInputConfig<Datum>) {
    this._config = merge(defaultCosmographTimelineConfig, config ?? {})
    this.timeline = new Timeline(targetElement, this._createTimelineConfig(config))
    this._cosmograph = cosmograph
    this._filter = this._config.filterType === FilterType.Nodes ? this._cosmograph.addNodesFilter() : this._cosmograph.addLinksFilter()
    this._filter.onDataAdded = () => {
      this._updateData()
    }
    this._updateDimension()
    this._updateData()
  }

  /**
   * Sets the configuration for the timeline.
   * @param config Configuration to be applied to the timeline.
   */
  public setConfig (config?: CosmographTimelineInputConfig<Datum>): void {
    const nextConfig = merge(defaultCosmographTimelineConfig, config ?? {})
    this.timeline.setConfig(this._createTimelineConfig(config))
    if (this._config.accessor?.toString() !== nextConfig.accessor?.toString()) {
      this._updateData()
    }
    this._config = nextConfig
  }

  /**  `getCurrentSelection`: Returns current brush selection in data units (`Date` or `number`). */
  public getCurrentSelection (): [Date, Date] | [number, number] | undefined {
    return this.timeline.getCurrentSelection()
  }

  /**  `getCurrentSelectionInPixels`: Returns current brush selection in pixels. */
  public getCurrentSelectionInPixels (): [number, number] | undefined {
    return this.timeline.getCurrentSelectionInPixels()
  }

  /**  `getBarWidth`: Returns computed bar width in pixels */
  public getBarWidth (): number {
    return this.timeline.getBarWidth()
  }

  /**  `getIsAnimationRunning`: Returns a boolean value indicating if the animation is running. */
  public getIsAnimationRunning (): boolean {
    return this.timeline.getIsAnimationRunning()
  }

  /**  `setSelection`: Set the selected range on a `Timeline`. Takes a selection range as a parameter, which can be a range of dates or a range of numbers if `TimelineData` is numeric. */
  public setSelection (selectionRange?: [Date, Date] | [number, number]): void {
    this.timeline.setSelection(selectionRange)
  }

  /**  `setSelectionInPixels`: Set the selected range on a `Timeline` in pixels. Takes an array containing two numeric values representing selection range in pixels. */
  public setSelectionInPixels (coordinates?: [number, number]): void {
    this.timeline.setSelectionInPixels(coordinates)
  }

  /**  `playAnimation`: If some interval is selected on `Timeline`, starts animation for it. The selected interval is moved forward by each timeline bar according to the speed passed in the `animationSpeed` of the `Timeline` `config`. */
  public playAnimation = (): void => {
    this.timeline.playAnimation()
  }

  /**  `pauseAnimation`: Pauses animation of selected timeline interval. */
  public pauseAnimation = (): void => {
    this.timeline.pauseAnimation()
  }

  /**  `stopAnimation`: Same as `pauseAnimation()`, but resets selection and returns `undefined` value for the `onBrush` callback. */
  public stopAnimation = (): void => {
    this.timeline.stopAnimation()
  }

  private _updateData (): void {
    const timelineData = this._filter.getAllValues() as TimelineData
    if (timelineData) this.timeline.setTimeData(timelineData)
    this.timeline.render()
    this.timeline.resize()
  }

  private _updateDimension (): void {
    const { _config: { accessor }, _filter } = this
    _filter.setAccessor(accessor as (d: CosmosInputNode | CosmosInputLink) => number)
  }

  private _applyFilter (selection?: [Date, Date] | [number, number]): void {
    const { _filter } = this
    if (selection) {
      _filter.applyFilter(d => (d as number | Date) >= selection[0] && (d as number | Date) <= selection[1])
    } else {
      _filter.clear()
    }
  }

  private _onBrush: Exclude<TimelineEvents['onBrush'], undefined> = (selection, isManuallySelected) => {
    this._applyFilter(selection)
    this._config.onSelection?.(selection, isManuallySelected)
  }

  private _onBarHover: Exclude<TimelineEvents['onBarHover'], undefined> = (...args) => {
    this._config.onBarHover?.(...args)
  }

  private _onAnimationPlay: Exclude<TimelineEvents['onAnimationPlay'], undefined> = (...args) => {
    this._config.onAnimationPlay?.(...args)
  }

  private _onAnimationPause: Exclude<TimelineEvents['onAnimationPause'], undefined> = (...args) => {
    this._config.onAnimationPause?.(...args)
  }

  /**  `getConfig`: Returns current `Timeline` configuration */
  public getConfig (): CosmographTimelineConfigInterface<Datum> {
    return this._config
  }

  public remove (): void {
    this.timeline.destroy()
  }

  private _createTimelineConfig (config?: CosmographTimelineInputConfig<Datum>): TimelineConfigInterface {
    return {
      ...config,
      events: {
        onBrush: this._onBrush.bind(this),
        onBarHover: this._onBarHover.bind(this),
        onAnimationPlay: this._onAnimationPlay.bind(this),
        onAnimationPause: this._onAnimationPause.bind(this),
      },
    }
  }
}

export type { CosmographTimelineConfigInterface, CosmographTimelineInputConfig }
