import { CosmosInputLink, CosmosInputNode } from '@cosmograph/cosmos'
import { Histogram, HistogramConfigInterface, HistogramEvents, merge } from '@cosmograph/ui'
import { Cosmograph, Filter } from '../cosmograph'
import { FilterType } from '../cosmograph/types'
import { CosmographHistogramInputConfig, CosmographHistogramConfigInterface, defaultCosmographHistogramConfig } from './config'

export class CosmographHistogram<OutDatum, InDatum = OutDatum> {
  private _cosmograph: Cosmograph<CosmosInputNode, CosmosInputLink>
  private _histogram: Histogram
  private _config: CosmographHistogramConfigInterface<OutDatum, InDatum>
  private _filter: Filter<CosmosInputNode | CosmosInputLink>

  public constructor (cosmograph: Cosmograph<CosmosInputNode, CosmosInputLink>, targetElement: HTMLElement, config?: CosmographHistogramInputConfig<OutDatum, InDatum>) {
    this._cosmograph = cosmograph
    this._config = merge(defaultCosmographHistogramConfig, config ?? {})
    this._histogram = new Histogram(targetElement, this._createHistogramConfig(config))
    this._filter = this._config.filterType === FilterType.Nodes ? this._cosmograph.addNodesFilter() : this._cosmograph.addLinksFilter()
    this._filter.onDataAdded = () => {
      this._updateData()
    }
    this._filter.onFiltered = () => {
      if (this._config.highlightCrossfiltered) this._updateDynamicData()
    }
    this._updateDimension()
    this._updateData()
  }

  /**
   * Sets the config for the histogram.
   * @param config Configuration to be applied to the histogram.
   */
  public setConfig (config?: CosmographHistogramInputConfig<OutDatum, InDatum>): void {
    const nextConfig = merge(defaultCosmographHistogramConfig, config ?? {})
    this._histogram.setConfig(this._createHistogramConfig(config))
    if (
      nextConfig.accessor !== this._config.accessor ||
      nextConfig.data !== this._config.data ||
      nextConfig.filterFunction !== this._config.filterFunction
    ) {
      this._updateDimension()
      this._updateData()
      // Re-set selection
      this.setSelection()
    }
    this._config = nextConfig
  }

  /**  `getCurrentSelection`: Returns current brush selection. */
  public getCurrentSelection (): number[] | undefined {
    return this._histogram.getCurrentSelection
  }

  /**  `getCurrentSelectionInPixels`: Returns current brush selection in pixels. */
  public getCurrentSelectionInPixels (): number[] {
    return this._histogram.getCurrentSelectionInPixels
  }

  /**  `getBarWidth`: Returns computed bar width in pixels */
  public getBarWidth (): number {
    return this._histogram.getBarWidth()
  }

  /**  `setSelection`: Set the selected range on a `Histogram`. Takes a numeric selection range in the X axis units as a parameter. */
  public setSelection (selection?: [number, number]): void {
    this._histogram.setSelection(selection)
  }

  /**
   * Returns current histogram configuration.
   */
  public getConfig (): CosmographHistogramConfigInterface<OutDatum, InDatum> {
    return this._config
  }

  /**
   * Destroy the histogram instance.
   */
  public remove (): void {
    this._histogram.destroy()
  }

  private _createHistogramConfig (config?: CosmographHistogramInputConfig<OutDatum, InDatum>): HistogramConfigInterface {
    return {
      ...config,
      events: {
        onBrush: this._onBrush.bind(this),
        onBarHover: this._onBarHover.bind(this),
      },
    }
  }

  private _updateDimension (): void {
    const { _config: { accessor, filterFunction, data, filterType }, _filter } = this
    if (!accessor) return
    const isUserDefinedFiltering = filterFunction && data
    if (isUserDefinedFiltering) {
      if (filterType === FilterType.Nodes) {
        _filter.setAccessor(d => (d as CosmosInputNode).id)
      } else {
        _filter.setAccessor(d => ([(d as CosmosInputLink).source, (d as CosmosInputLink).target]))
      }
    } else {
      _filter.setAccessor(accessor as (d: CosmosInputNode | CosmosInputLink) => number)
    }
  }

  private _applyFilter (selection?: [number, number]): void {
    const { _filter, _config: { filterFunction, data, filterType } } = this
    if (selection) {
      const isUserDefinedFiltering = filterFunction && data
      if (isUserDefinedFiltering) {
        const crossSelectedRecords = _filter.getFilteredRecords() as OutDatum[]
        if (filterType === FilterType.Nodes) {
          const filteredNodes = filterFunction(selection, data, crossSelectedRecords) as CosmosInputNode[]
          const filteredNodeIds = new Set(filteredNodes.map(d => d.id))
          _filter.applyFilter(d => filteredNodeIds.has(d as string))
        } else {
          const filteredLinks = filterFunction(selection, data, crossSelectedRecords) as CosmosInputLink[]
          const filteredLinkIds = new Set(filteredLinks.map(d => [d.source, d.target]).flat())
          _filter.applyFilter(d => {
            const source = (d as [string, string])?.[0]
            const target = (d as [string, string])?.[1]
            return filteredLinkIds.has(source) && filteredLinkIds.has(target)
          })
        }
      } else {
        _filter.applyFilter(d => (d as number) >= selection[0] && (d as number) <= selection[1])
      }
    } else _filter.clear()
  }

  private _updateData (): void {
    const { _config: { data, filterFunction, accessor } } = this
    const isUserDefinedFiltering = filterFunction && data
    const histogramData = (isUserDefinedFiltering && accessor) ? data.map(accessor) : this._filter.getAllValues()
    if (histogramData) this._histogram.setHistogramData(histogramData as number[], this._config.customExtent)
  }

  private _updateDynamicData (): void {
    const dynamicData = this._filter.getFilteredValues() as number[]
    this._histogram.setHighlightedData(dynamicData)
  }

  private _onBrush: Exclude<HistogramEvents['onBrush'], undefined> = (selection, isManuallySelected) => {
    this._applyFilter(selection)
    this._config.onSelection?.(selection, isManuallySelected)
  }

  private _onBarHover: Exclude<HistogramEvents['onBarHover'], undefined> = (...args) => {
    this._config.onBarHover?.(...args)
  }
}

export type { CosmographHistogramConfigInterface, CosmographHistogramInputConfig }
